import axios from "axios";

const instanse = axios.create({
  baseURL: 'https://social-network.samuraijs.com/api/1.0/',
  withCredentials: true,
  headers: {
    'API-KEY': '599053cd-e7b8-4a40-8c86-544d9274b6a1'
  }
})

export const USERS_API = {
  async getUsers(pageSize = 1, currentPage= 5) {
    const users = await instanse.get(`users?count=${pageSize}&page=${currentPage}`);
    const usersData = await users.data;
    return usersData;
  },

  deleteUser(userId) {
    return instanse.delete(`follow/${userId}`).then(res => res.data)
  },

  postUser(userId) {
    return instanse.post(`follow/${userId}`).then(res => res.data)
  }
};


export const PROFILE_API = {
  async getProfile(profileId) {
    return await instanse.get(`profile/${profileId}`)
  },

  async getStatus(userId) {
    return await instanse.get(`profile/status/${userId}`)
  },

  async updateStatus(statusText) {
    return await instanse.put(`profile/status`, {status: statusText})
  },

  async savePhoto(photo) {
    const formData = new FormData();
    formData.append('image', photo);
    return await instanse.put(`profile/photo`, formData, {
      headers: {
        "Content-type": "multipart/form-data"
      }
    })
  },

  async editProfile(props) {
    return await instanse.put(`profile`, {...props})
  }
}

export const AUTH_API = {
  getAuthUser() {
    return instanse.get(`auth/me`)
  },

  async postAuthUser(email, password, rememberMe, captcha) {
    return await instanse.post(`auth/login`, {
      email,
      password,
      rememberMe,
      captcha
    })
  },

  async deleteAuthUser() {
    return await instanse.delete(`auth/login`)
  },
}

export const SECURITY_API = {
  getCaptchaUrl() {
    return instanse.get(`security/get-captcha-url`)
  }
}