import { PROFILE_API } from "../api/api";

const ADD_POST = 'profile/ADD-POST';
const SET_USER_PROFILE = 'profile/SET_USER_PROFILE-POST';
const GET_USER_STATUS = 'profile/GET_USER_STATUS';
const DELETE_POST = 'profile/DELETE_POST';
const SAVE_PHOTO = 'profile/SAVE_PHOTO';
const UPDATE_PROFILE = 'profile/UPDATE_PROFILE';
const SHOW_ERROR_MESSAGE = 'profile/SHOW_ERROR_MESSAGE';

const initialState = {
  postData: [
    {
      id: 1,
      message: 'Hi, how are you?',
      likeCount: 1
    },
    {
      id: 2,
      message: 'I want to go to park',
      likeCount: 2
    },
    {
      id: 3,
      message: 'What about you?',
      likeCount: 5
    },
  ],
  
  userInfo: '',

  statusText: '',

  errorMessage: '',

  userId: 29384,

};

const profileReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_POST: {
      if(action.text === '') return state;

      const data = {
        id: Math.random(),
        message: action.text,
        likeCount: 0
      };

      return {
        ...state, 
        postData: [...state.postData, data],
        newPostText: '',
      };
    }

    case DELETE_POST: {
      if(!action.postId) return state;

      return {
        ...state, 
        postData: state.postData.filter(post => post.id !== action.postId),
      };
    }

    case SET_USER_PROFILE: {
      return {
        ...state,
        userInfo: action.userInfo
      }
    }

    case GET_USER_STATUS: {
      return {
        ...state,
        statusText: action.statusText
      }
    }

    case SAVE_PHOTO: {
      return {
        ...state,
        userInfo: {...state.userInfo, photos: action.photos} 
      }
    }

    case UPDATE_PROFILE: {
      return {
        ...state,
        userInfo: {...state.userInfo, ...action.data} 
      }
    }

    case SHOW_ERROR_MESSAGE: {
      return {
        ...state,
        errorMessage: action.message
      }
    }

    default:
      return state;
  }
};

export const addPostActionCreator = (text) => {
  return {
    type: ADD_POST,
    text: text,
  }
};


export const setUserProfile = (userInfo) => ({type: SET_USER_PROFILE, userInfo});
export const setUserStatus = (statusText) => ({type: GET_USER_STATUS, statusText});
export const deletePost = (postId) => ({type: DELETE_POST, postId})
export const savePhotoSuccess = (photos) => ({type: SAVE_PHOTO, photos})
export const updateProfileSuccess = (data) => ({type: UPDATE_PROFILE, data})
export const showErrorMessage = (message) => ({type: SHOW_ERROR_MESSAGE, message})


export const getProfile = (profileId) => {
  return async (dispatch) => {
    const res = await PROFILE_API.getProfile(profileId);
    dispatch(setUserProfile(res.data));
  }
}

export const getUserStatus = (userId) => {
  return async (dispatch) => {
    const res = await PROFILE_API.getStatus(userId);
    dispatch(setUserStatus(res.data));
  }
}

export const updateUserStatus = (statusText) => {
  return async (dispatch) => {
    const res = await PROFILE_API.updateStatus(statusText);
    if(res.data.resultCode === 0) {
      dispatch(setUserStatus(statusText));
    }
  }
}

export const savePhotoThunkCreator = (file) => {
  return async (dispatch) => {
    const res = await PROFILE_API.savePhoto(file);
    if(res.data.resultCode === 0) {
      dispatch(savePhotoSuccess(res.data.data.photos));
    }
  }
}

export const updateProfileThunkCreator = (requestData, toggleEditMode) => {
  return async (dispatch) => {
    const res = await PROFILE_API.editProfile(requestData);
    if(res.data.resultCode === 0) {
      dispatch(getProfile(requestData.userId));
      dispatch(showErrorMessage(''));
      toggleEditMode();
    } else {
      dispatch(showErrorMessage(res.data.messages[0]))
    }
  }
}


export default profileReducer;